import { Injectable } from '@angular/core';
import { IColumn, ICombinedColumn } from '../../../../../../../Bamba.Shared/src/models/column';
import { MetaDataDefinition } from '../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { IFolder } from '../../../blocks/services/models';
import { Filter, IFilterValue } from './../../../blocks/models/filter';
import { UserStateService } from './../../../blocks/services/user-state.service';

@Injectable({
  providedIn: 'root',
})
export class ProductItemService {
  constructor(private userStateService: UserStateService) {}

  getColumns(folder: IFolder): Array<ICombinedColumn> {
    const folderColumns: IColumn[] = [];

    if (folder && folder.columns) {
      folder.columns.forEach((c) => folderColumns.push(c));
    }

    if (folder.includeMetaDataAsColumns) {
      this.userStateService.user.metaData.forEach((md) => folderColumns.push(MetaDataDefinition.asIColumn(md)));
    }

    const columns: Array<ICombinedColumn> = [];

    for (const column of folder.defaultColumns) {
      const correspondingColumn = folderColumns.find((c) => c && column && c.id == column.id);
      if (!correspondingColumn) continue;

      columns.push({ ...correspondingColumn, ...column });
    }

    return columns;
  }

  getFilters = (folder: IFolder): IFilterValue[] => {
    return folder.filters.map((filter) => {
      const f = filter as Filter;
      return { id: f.id, value: f.value };
    });
  };
}
