import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { ICombinedColumn } from '../../../../../../../Bamba.Shared/src/models/column';
import { TableItem } from '../../../../../../../Bamba.Shared/src/models/table';
import { ActionService } from '../../../blocks/services/action.service';
import { HydraApiService } from './../../../blocks/services/hydra-api.service';
import { Control, IAction, IFolder } from './../../../blocks/services/models';
import { SpinnerStateService } from './../../../blocks/services/spinner-state.service';
import { UserStateService } from './../../../blocks/services/user-state.service';
import { ProductItemService } from './../services/product-item.service';

@Component({
  selector: 'app-folder-item',
  templateUrl: './folder-item.component.html',
  styleUrls: ['./folder-item.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FolderItemComponent implements OnInit {
  item: TableItem = null;
  folder: IFolder;
  columns: ICombinedColumn[] = [];
  actionsControllers: Control[] = [];
  params: ParamMap;

  constructor(
    private route: ActivatedRoute,
    private hydraApiService: HydraApiService,
    private userStateService: UserStateService,
    private spinnerService: SpinnerStateService,
    private translate: TranslateService,
    private toastr: ToastrService,
    private actionService: ActionService,
    private productItemService: ProductItemService
  ) {}

  ngOnInit() {
    this.route.paramMap.subscribe((params) => {
      this.params = params;
      this.getFolder();
      this.getItemDetails();
    });
  }

  getItemDetails() {
    this.spinnerService.show(this.translate.instant('Common.Loading'), document.documentElement);
    this.item = null;
    this.hydraApiService.getItemDetails(this.params.get('itemId'), this.itemLoadedSuccessfully, this.itemLoadFailed);
  }

  itemLoadedSuccessfully = (data) => {
    this.item = new TableItem(data, this.columns, this.translate);
    this.createItemActionsControllers();
    this.spinnerService.hide();
  };

  createItemActionsControllers = () => {
    if (!this.item) return [];

    const actions = this.item.data.allowedActions.filter((a) => a.id !== this.item.data.defaultAction.id);

    actions.forEach((action) => {
      this.actionsControllers.unshift(
        new Control(() => this.executeAction(action), action.name, 'btn btn-light', '', action.name, 'btn', false)
      );
    });
  };

  itemLoadFailed = (error) => {
    this.item = null;
    this.spinnerService.hide();
    this.toastr.error(error.message);
  };

  getFolder = (): void => {
    const folderId = this.params.get('itemId').split('~')[1];
    if (!folderId) return;

    this.folder = this.userStateService.findFolderById(folderId);
    if (this.folder == null) return;

    this.columns = this.productItemService.getColumns(this.folder);
  };

  executeAction(action: IAction) {
    const itemActionHandler = this.actionService.createItemActionHandler();
    itemActionHandler.invoke(this.folder.id, action, [this.item.data], this.productItemService.getFilters(this.folder));
  }
}
