import { Component, Input, OnInit } from '@angular/core';
import { ICombinedColumn } from '../../../../../../../Bamba.Shared/src/models/column';
import { TableItem } from '../../../../../../../Bamba.Shared/src/models/table';
import { IFolder } from './../../../blocks/services/models';

@Component({
  selector: 'app-simplified-table-item',
  templateUrl: './simplified-table-item.component.html',
  styleUrls: ['./simplified-table-item.component.scss'],
})
export class SimplifiedTableItemComponent implements OnInit {
  @Input() item: TableItem;
  @Input() folder: IFolder;
  @Input() columns: ICombinedColumn[] = [];

  get columnsArrangement() {
    if (!this.folder) return [];
    return this.folder.simplifiedColumnsArrangement;
  }

  constructor() {}

  ngOnInit() {}

  getUIColumn(uiColumnId: string) {
    return this.columns.find((col) => col.id === uiColumnId);
  }
}
