import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FaxMobileComponent } from './fax-mobile.component';
import { SendComponent } from './send/send.component';
import { ProductItemComponent } from './product-item/product-item.component';
import { FolderItemComponent } from './folder-item/folder-item.component';

const routes: Routes = [
  { path: '', component: FaxMobileComponent, pathMatch: 'full' },
  { path: 'send', component: SendComponent, pathMatch: 'full' },
  {
    path: 'view/:itemId',
    component: FolderItemComponent,
    pathMatch: 'full',
  },
  {
    path: 'product/:product/:folder',
    component: ProductItemComponent,
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FaxMobileRoutingModule {}
