import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { ICombinedColumn, ItemTableDataSource, ItemsRequest, ItemsResult, TableItem } from 'shared-code-lib';
import { ActionService } from '../../../blocks/services/action.service';
import { ApiService } from '../../../blocks/services/api.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { ProductItemService } from '../services/product-item.service';
import { IFolder } from './../../../blocks/services/models';

@Component({
  selector: 'app-product-item',
  templateUrl: './product-item.component.html',
  styleUrls: ['./product-item.component.scss'],
})
export class ProductItemComponent implements OnInit, OnDestroy {
  folder: IFolder;
  items: TableItem[] = [];
  columns: ICombinedColumn[] = [];
  total = 0;
  pageSize = 25;
  pageNumber = 1;
  params: Params;
  loading = true;

  subscription = new Subscription();

  constructor(
    private route: ActivatedRoute,
    private userState: UserStateService,
    private translate: TranslateService,
    private api: ApiService,
    private toastr: ToastrService,
    private spinnerState: SpinnerStateService,
    private actionService: ActionService,
    private productItemService: ProductItemService
  ) {}

  ngOnInit() {
    this.subscription.add(
      this.route.params.subscribe((params: Params) => {
        this.params = params;
        this.openFolder();
      })
    );

    this.subscription.add(
      this.spinnerState.visibilityChanged.subscribe((data) => {
        this.loading = data.visible;
      })
    );
  }

  openFolder = (): void => {
    this.folder = this.userState.findFolderById(this.params.folder);
    if (this.folder == null) return;

    this.onLoadFolder();
  };

  onLoadFolder = (): void => {
    this.columns = this.productItemService.getColumns(this.folder);
    this.loadItems();
  };

  loadItems = (): void => {
    if (this.items.length && this.items.length === this.total) return;

    const request = new ItemsRequest();

    const sortColumn = this.folder.columns.find((c) => c.id == this.folder.defaultSortByColumn.columnId);

    request.pageNumber = this.pageNumber;
    request.pageSize = this.pageSize;
    request.sortBy = sortColumn ? sortColumn.sortByFieldName : '';
    request.sortByDirection = this.folder.defaultSortByColumn.direction;
    request.filters = this.productItemService.getFilters(this.folder);

    this.spinnerState.show(this.translate.instant('Common.LoadingData'), document.documentElement);

    const dataSource = new ItemTableDataSource(this.folder.product.id, this.params.folder, this.api);
    dataSource.loadItems(request, this.itemsLoad, this.itemsLoadFailed);
  };

  itemsLoad = (results: ItemsResult): void => {
    this.spinnerState.hide(this.translate.instant('Common.LoadingData'));

    if (!results.success) {
      this.toastr.error(results.errors[0]);
      return;
    }

    const newItems = results.items.map((i) => new TableItem(i, this.columns, this.translate));
    this.items = [...this.items, ...newItems];
    this.total = results.total;
    this.pageNumber++;
  };

  itemsLoadFailed = (): void => {
    this.spinnerState.hide(this.translate.instant('Common.LoadingData'));
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  };

  executeDefaultAction(item: TableItem) {
    const defaultAction = item.data.defaultAction;
    if (!defaultAction) {
      this.toastr.error(this.translate.instant('interFax-mobile.view-action-is-not-allowed'));
      return;
    }

    const itemActionHandler = this.actionService.createItemActionHandler();
    itemActionHandler.invoke(
      this.params.folder,
      defaultAction,
      [item.data],
      this.productItemService.getFilters(this.folder)
    );
  }

  onPasswordUpdated() {
    this.openFolder();
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
