import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MobileAction } from '../../../../../../Bamba.Shared/src/models/mobile-action';
import { SessionStorageService } from '../../../../../../Bamba.Shared/src/services/session-storage.service';
import { UserStateService } from './../../blocks/services/user-state.service';

@Component({
  selector: 'app-fax-mobile',
  templateUrl: './fax-mobile.component.html',
  styleUrls: ['./fax-mobile.component.scss'],
})
export class FaxMobileComponent implements OnInit {
  constructor(
    private userStateService: UserStateService,
    private router: Router,
    private sessionStorage: SessionStorageService
  ) {}

  actions: MobileAction[] = [];

  ngOnInit() {
    this.actions = this.getInterFaxMobileActions();
  }

  getInterFaxMobileActions() {
    const interFaxProduct = this.userStateService.user.products.find((product) => product.id === 'InterFAX');
    if (!interFaxProduct) return [];

    return interFaxProduct.mobileActions.filter((action) => action.isInterFaxMobileAction);
  }

  onSubmitActionSelected(action: MobileAction) {
    this.router.navigateByUrl(action.route);
  }

  addToHomePromptVisible = () => document.querySelector('.add-to-home-screen-message-ios');

  switchToDesktop = (): void => {
    this.sessionStorage.setIsInterFaxMobile(false);
  };
}
